const ModalAlert = (props) => {
  return ( 
    <div style={{ maxHeight: `${80}vh`, overflow: 'auto' }}>
      <div>
        <button type='button' onClick={props.onCancel}>×</button>
      </div>
      <div style={{whiteSpace: 'pre-wrap', lineBreak: "anywhere", width: `${720}px`, maxWidth: `${90}vw`}}>
        {props.message}
      </div>
      <div>
        <button type='button' onClick={props.onConfirm}>Ok</button>
      </div>
    </div>
  );
}

ModalAlert.defaultProps = {
  message: 'NO MESSAGE',
  onConfirm: () => {},
  onCancel : () => {},
}
 
export default ModalAlert;