import React, { useEffect } from 'react';
import cs from '../../styles/cs';
import UtilCommon from '../../utils/util_common';

const ModalWrapper = (props) => {
  const [visible, setVisible] = React.useState(false);
  const [opacity, setOpacity] = React.useState(false);
  const [modals, setModals] = React.useState([]);

  useEffect(() => {
    async function showModal(animation_duration) {
      setOpacity(false);
      setVisible(true);
      setModals(props.modals);
      await UtilCommon.delay(animation_duration);
      setOpacity(true);
    };

    async function hideModal(animation_duration) {
      setOpacity(false);
      await UtilCommon.delay(animation_duration);
      setVisible(false);
      setModals(props.modals);
    }

    if (props.modals.length > modals.length)
      showModal(100);
    else if (props.modals.length < modals.length)
      hideModal(200);

  }, [props.modals, modals]);

  if (!modals.length)
    return null;

  return ( 
    <div className={cs({ 'modal-wrapper': true, visible, opacity })}>
      <div className="modal-wrapper-backdrop" onClick={props.closeCurrentModal}/>
      <div className="modal-wrapper-container">
        {modals[0]}
      </div>
    </div>
  );
}

ModalWrapper.defaultProps = {
  modals: [],
  closeCurrentModal: () => console.log('closeCurrentModal() is not defined.')  
}
 
export default ModalWrapper;